@font-face {
  font-family: 'Roboto';
  src: url('./source/fonts/Roboto-Regular.ttf') format('truetype');
}

html, #root {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font: 14px/1.4, Roboto,PingFang SC,Microsoft YaHei,Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

div, span {
  box-sizing: border-box;
}

ul {
  box-sizing: border-box;
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  box-sizing: border-box;
  margin: 0;
}




