@import (reference) "../../../../color";
@import (reference) "../stock-chart-common";

.tools(
  @second-font-color,
   @border-color,
    @box-background-color,
     @hover-background-color
) {
  width: @tools-width;
  height: 100%;
  border-right: solid 1px @border-color;
  .tools-list {
    .tools-list-delete-row {
      padding-top: 6px;
      border-top: solid 1px @border-color;
    }
    .tools-list-row {
      display: flex;
      flex-direction: row;
      justify-content: center;
      position: relative;
      margin-top: 6px;
      .tools-list-row-icon {
        width: 22px;
        height: 22px;
        fill: @second-font-color;
        .stroke {
          stroke: @second-font-color;
          stroke-width: 1;
          fill: transparent;
        }
      }
      .tools-list-row-icon-wrapper {
        padding: 4px;
        border-radius: 2px;
        cursor: pointer;
        &:hover {
          background-color: @hover-background-color;
        }
        &:hover + .tools-list-row-arrow-wrapper {
          .tools-list-row-arrow {
            transform: rotate(-90deg) scaleY(1) translateY(0);
          }
        }
      }
      .tools-list-row-arrow-wrapper {
        position: absolute;
        right: 0;
        top: 0;
        width: 10px;
        height: 100%;
        justify-content: flex-end;
        cursor: pointer;
        border-top-left-radius: 2px;
        border-bottom-left-radius: 2px;
        outline: none;
        &:hover {
          background-color: @hover-background-color;
          .tools-list-row-arrow {
            transform: rotate(-90deg) scaleY(1) translateY(0);
          }
        }
        .tools-list-row-arrow {
          width: 8px;
          height: 8px;
          fill: @second-font-color;
          transition: all .2s;
          transform: rotate(-90deg) scaleY(0) translateY(-6px);
        }
      }
      .tools-list-row-extension-list {
        position: absolute;
        left: 100%;
        top: 0;
        border-radius: 2px;
        padding: 6px 0;
        background-color: @box-background-color;
        box-shadow: 0 6px 12px 0 rgba(0, 0, 0, .5);
        z-index: 10;
        .tools-list-row-extension-list-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          height: 40px;
          font-size: 14px;
          padding: 0 14px;
          color: @second-font-color;
          white-space: nowrap;
          cursor: pointer;
          &:hover {
            background-color: @hover-background-color;
          }
          .tools-list-row-extension-list-row-text {
            display: inline-block;
            margin-left: 16px;
          }
        }
      }
    }
  }
}

.tools-dark {
  .tools(
    @second-font-color-dark,
    @border-color-dark,
    @drop-down-background-color-dark,
    @hover-background-color-dark
  )
}

.tools-light {
  .tools(
    @second-font-color-light,
    @border-color-light,
    @drop-down-background-color-light,
    @hover-background-color-light
  )
}

@media screen and (max-width: 450px) {
  .tools-dark, .tools-light {
    display: none;
  }
}
