@import (reference) "../../../color";
@import (reference) "../common";

.top-bar (@main-font-color, @second-font-color, @border-color) {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 14px;
  height: @top-bar-height;
  color: @second-font-color;
  border-bottom: solid 1px @border-color;
  padding: 0 20px 0 12px;
  .top-bar-periods {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .top-bar-periods-small {
    display: none;
    flex-direction: row;
    height: 100%;
  }

  .top-bar-tools {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
  }

  .top-bar-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 8px;
    cursor: pointer;
    &:hover {
      color: @main-font-color;
    }
    &-selected {
      color: @main-blue-color!important;
      font-weight: bold;
    }
  }
  .top-bar-icon-item {
    padding: 0 4px;
    .top-bar-icon {
      display: inline-block;
      font-size: 18px;
      margin-right: 6px;
    }
  }

  .top-bar-split-line {
    display: inline-block;
    margin-left: 8px;
    margin-right: 8px;
    height: 100%;
    width: 1px;
    background-color: @border-color;
  }
  .version-container {
    height: 34px;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: @main-font-color;
    text-decoration: none;
    margin-left: 10px;
    cursor: pointer;
    .github-icon {
      font-size: 20px;
    }
  }
}

.top-bar-dark {
  .top-bar(
    @main-font-color-dark,
    @second-font-color-dark,
    @border-color-dark
  )
}

.top-bar-light {
  .top-bar(
    @main-font-color-light,
    @second-font-color-light,
    @border-color-light
  )
}

@media screen and (max-width: 450px) {
  .top-bar-light, .top-bar-dark {
    padding: 0 10px 0 4px;
    .top-bar-periods {
      display: none;
    }
    .top-bar-periods-small {
      display: flex;
    }
    .top-bar-tools {
      display: none;
    }
    .top-bar-item {
      padding: 0 6px;
    }
  }
}
